*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

#root{
    display: flex;
    justify-content: center;
    flex-direction: column;
    
}

.App{
    width: 800px; 
    margin: auto; 
}

.post{
    display: flex;
    padding: 15px;
    border: 2px solid teal;
    margin: 15px 0;
    justify-content: space-between;
    align-items: center;
}

.postList__text{
    text-align: center;
}

  
.post-enter {
    transform: translateX(-350px);
  }
.post-enter-active {
    transform: translateX(0px);
    transition: all 500ms ease-in;
  }
.post-exit {
    opacity: 1;
  }
.post-exit-active {
    transform: translateX(-350px);
    transition: all 500ms ease-in;
  }
  .post__btns{
    display: flex;
  }

  .post__comments{
    margin: 15px 25px;
  }
  .post__comments h5{
    
  }
  .post__comments p{
    font-weight: 400;
    font-size: 18px;
  }
.post__comments .post__comments__email{
  font-size: 10px;
  text-align: end;
  color: dodgerblue;
  }

.page__wrepper{
    margin-top: 30px;
    display: flex;
  }
  
.page__wrepper .page{
  padding: 10px 15px;
  margin-right: 10px;
  margin-bottom: 10px;
  background: #fff;
  color: teal;
  border: 1px solid teal;
  cursor: pointer;
}
.page:hover{
  background: rgb(198, 213, 227);
}

.page.page__current{
  background-color: teal;
  color: #fff;
}

.navbar{
  height: 50px;
  display: flex;
  align-items: center;
  width: 100%;
  padding: 0 40px;
  background: rgb(198, 213, 227);
}

.navbar__links{
  margin-left:auto ;

}

.navbar__links a{
  /* margin: 0 10px; */
  font-weight: 600;
  font-size: 20px;
  color: teal;
  text-decoration: none;
  padding: 5px 25px;
  border-radius: 10px;
}

.navbar__links a:hover{
  background: teal;
  color: rgb(198, 213, 227);
}